.messages-window {
    grid-row: 1;
    grid-column: 1;
    height: 100%;
    z-index: 10;
    overflow: auto;
}

.messages-container {
    display: flex;
    width: 100%;
}

.messages-option {
    max-height: 64px;
}

.own-messages {
    margin: 8px;
    margin-left: auto;
    max-width: 500px;
    height: auto;

    padding: 4px 16px 4px 16px;

    background: #6FE4F5;
    border-radius: 1em;
}

.friend-messages {
    margin: 8px;
    max-width: 500px;
    height: auto;

    padding: 4px 16px 4px 16px;

    background: #6DF473;
    border-radius: 1em;
}

.system-messages {
    text-align: center;
    width: 100%;
    margin: 8px;
    height: auto;

    padding: 4px 16px 4px 16px;

    background: #ECCCC1;
}